* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

/* ====================================================== */

.country-list {
  list-style: none;
}
.country-list__new-country {
  display: flex;
}
.country-list__country-flag {
  margin-right: 20px;
  margin-bottom: 20px;
}

.country-list__item--name {
  font-size: xx-large;
}

.country-info__list {
  list-style: none;
  display: flex;
  flex-direction: column;
}
.country-info__item {
  font-size: x-large;
}
.country-info__item--categories {
  font-size: x-large;
}
